import { useState, useRef } from "react";
import './dnd.css';
import { COMPONENT } from './constants';
import { DndProvider, useDrag, useDrop } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";
import { TouchBackend } from "react-dnd-touch-backend";
import { Slick } from "../components/react-slick";

const MovableItem = ({ name, index, moveCardHandler, setItems }) => {
  /* drag 가능한 여러 항목 */
  const changeItemColumn = (currentItem, columnName) => {
    setItems((prevState) => {
      return prevState.map(e => {
        return {
          ...e,
          column: e.name === currentItem.name ? columnName : e.column,
        }
      })
    });
  }

  const ref = useRef(null);
  /* 위치 이동 자동 drop 자동 정렬일껄? */
  const [, drop] = useDrop({
    accept: COMPONENT,
    hover(item, monitor) {
      if (!ref.current) {
        return;
      }
      const dragIndex = item.index;
      const hoverIndex = index;
      console.log(dragIndex, hoverIndex);
      // Don't replace items with themselves
      if (dragIndex === hoverIndex) {
        return;
      }
      // Determine rectangle on screen
      const hoverBoundingRect = ref.current?.getBoundingClientRect();
      // Get vertical middle
      const hoverMiddleY = (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2;
      // Determine mouse position
      const clientOffset = monitor.getClientOffset();
      // Get pixels to the top
      const hoverClientY = clientOffset.y - hoverBoundingRect.top;
      // Only perform the move when the mouse has crossed half of the items height
      // When dragging downwards, only move when the cursor is below 50%
      // When dragging upwards, only move when the cursor is above 50%
      // Dragging downwards
      if (dragIndex < hoverIndex && hoverClientY < hoverMiddleY) {
        console.log("downwards");
        return;
      }
      // Dragging upwards
      if (dragIndex > hoverIndex && hoverClientY > hoverMiddleY) {
        console.log("upwards");
        return;
      }
      // Time to actually perform the action
      moveCardHandler(dragIndex, hoverIndex);
      // Note: we're mutating the monitor item here!
      // Generally it's better to avoid mutations,
      // but it's good here for the sake of performance
      // to avoid expensive index searches.
      item.index = hoverIndex;
    },
  });

  /* drag */
  const [{ isDragging }, drag] = useDrag({
    type: COMPONENT,
    item: {
      index,
      name
    },
    end: (item, monitor) => {
      const dropResult = monitor.getDropResult();
      if (dropResult && dropResult.name === 'Column 1') {
        changeItemColumn(item, 'Column 1')
      } else {
        changeItemColumn(item, 'Column 2')
      }
    },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  });

  const opacity = isDragging ? 0.4 : 1; /*  */

  /*  */
  //console.log(ref);
  drag(drop(ref));

  return (
    name === 'Item2' ?
      <div ref={ref} className="movable-item" id={name} style={{ opacity }}>
        <Slick />
      </div >
      :
      <div ref={ref} className="movable-item" id={name} style={{ opacity }}>
        {name}
      </div>
  )

}

/* drop 할 item */
const Column = ({ children, className, title }) => {
  const [, drop] = useDrop({
    accept: COMPONENT,
    drop: () => ({ name: title }),
  });

  return (
    <div ref={drop} className={className}>
      {/* {title} */}
      {children}
    </div>
  )
}

export const Dnd = () => {
  const [items, setItems] = useState([
    { id: 1, name: 'Item1', column: 'Column 1' },
    { id: 2, name: 'Item2', column: 'Column 1' },
    { id: 3, name: 'Item3', column: 'Column 1' },
    { id: 4, name: 'Item4', column: 'Column 1' },
  ]);

  const isMobile = window.innerWidth < 600;

  /* item 위치이동 */
  const moveCardHandler = (dragIndex, hoverIndex) => {
    const dragItem = items[dragIndex]; //내가 선택한 item 값

    if (dragItem) {
      setItems((prevState => {
        const coppiedStateArray = [...prevState];
        // remove item by "hoverIndex" and put "dragItem" instead (hoverIndex값 삭제하고 )
        const prevItem = coppiedStateArray.splice(hoverIndex, 1, dragItem);
        // remove item by "dragIndex" and put "prevItem" instead
        coppiedStateArray.splice(dragIndex, 1, prevItem[0]);
        return coppiedStateArray;
      }));
    }
  };

  // item을 각 column에 드래그 할때 체크하는 메소드
  const returnItemForColumn = (columnName) => {
    return items
      .filter((item) => item.column === columnName)
      .map((item, index) => (
        < MovableItem
          key={item.id}
          name={item.name}
          setItems={setItems}
          index={index}
          moveCardHandler={moveCardHandler}
        />
      ))
  };
  /* const [isFistColumn, SetIsFistColumn] = useState(true); */
  /* const Item = <MovableItem SetIsFistColumn={SetIsFistColumn} />; */

  return (
    <div className="container">
      <DndProvider backend={isMobile ? TouchBackend : HTML5Backend}>
        <Column title='Column 1' className='column1 first-column'>
          {returnItemForColumn('Column 1')}
        </Column>
        <Column title='Column 2' className='column2 Second-column'>
          {returnItemForColumn('Column 2')}
        </Column>
      </DndProvider>
    </div>
  );
}

export default Dnd;